.header {
  text-align: center;
}

.carousel-container {
  margin: 10px;
}

.selected-image {
  width: 100%;
  height: 500px;
  margin-bottom: 8px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel {
  position: relative;
  margin-bottom: 0px;
}

.carousel__images {
  display: flex;
  max-width: 100%;
  overflow-x: hidden;
}

.carousel__image-selected {
  border: 3px solid #ffa700 !important;
}

.carousel__image {
  margin-right: 10px;
  height: 150px;
  min-width: 150px;
  border: 3px solid #ffa70000;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carousel__button {
  position: absolute;
  top: 40%;
}

.carousel__button-left {
  left: 10px;
}

.carousel__button-right {
  right: 10px;
}

@media (max-width: 375px) {
  .selected-image {
    height: 250px;
    width: 300px;
    margin: 0px;
    padding: 0px;
  }
  .carousel__image {
    height: 100px;
    width: 100px;
    margin: 0px;
    padding: 0px;
  }
  .carousel-container {
    margin: 0px;
    padding: 0px;
  }
  .mobile-desc {
    display: none;
  }
}

.sidebar {
  position: fixed;
  top: 10;
  right: -57.5vw;
  width: 57.5vw;
  height: 100%;
  background-color: #fff;
  transition: right 1000ms ease-in-out;
  z-index: 1;
}
.sidebar.active {
  right: 0;
}

.sidebar ul {
  margin: 0;
  padding: 0;
}
.sidebar li {
  list-style: none;
}
.sidebar li a {
  font-size: 18px;
  text-decoration: none;
  padding: 10px;
  display: block;
  color: #fff;
}

.hamburger {
  border: none;
  outline: 0;
  width: 50px;
  position: absolute;
  left: -50px;
  background-color: #fff;
  z-index: 1;
}

.hamburger:after,
.hamburger:before,
.hamburger div {
  background-color: #fff;
  height: 5px;
  margin: 7px 0;
  border-radius: 3px;
  content: "";
  display: block;
  transition: all 900ms ease-in-out;
  z-index: 1;
}

.sidebar.active .hamburger:before {
  -webkit-transform: translateY(12px) rotate(135deg);
          transform: translateY(12px) rotate(135deg);
}
.sidebar.active .hamburger::after {
  -webkit-transform: translateY(-12px) rotate(-135deg);
          transform: translateY(-12px) rotate(-135deg);
}
.sidebar.active .hamburger div {
  -webkit-transform: scale(0);
          transform: scale(0);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(248, 246, 253);
}

body,
input,
textarea,
button {
  font-family: "Inter", sans-serif;
}

.app {
  width: 400px;
  margin: 0 auto;
  height: 100vh;
}

/* Contact.js */
.form {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form > h1 {
  margin-bottom: 30px;
}

.form > input,
textarea {
  padding: 20px;
  border-radius: 3px;
  /* box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.137); */
  margin-bottom: 20px;
  border: 1px solid lightgray;
  /* border: none; */
  background: #fff;
  font-size: 16px;
  color: rgb(0, 0, 32);
  outline: none;
}

.form > input:focus,
textarea:focus {
  border: 1px solid rgb(0, 0, 196);
}

.form > textarea {
  height: 150px;
  max-width: 400px;
  min-height: 100px;
}

.form > label {
  padding-bottom: 10px;
  color: rgb(0, 0, 32);
  font-weight: bold;
  text-align: left;
  padding-left: 5px;
}

.form > button {
  padding: 15px;
  border: none;
  font-weight: 500;
  font-size: 16px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  transition: 0.2s ease-in-out;
  margin-top: 10px;
}


.form > button:hover {
   background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)";
}

.contact {
    width: 75vw;
    /* margin: auto; */
    margin-left: 5em;
    margin-top: 1em;
    /* text-align: left; */
}

.title {
    font-size: 20px;
    margin: 2em 1em 1em 0em;
}

@media (min-width: 875px) {
    .contact {
        width: 50vw;
        margin: auto;
    }
    .title {
        font-size: 24px;
    }

}   
