*,
*:after,
*:before {
  margin: 0;
  padding: 0;
}
.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}
.flag {
  width: 100%;
}
:root {
  --bg: hsl(0, 0%, 98%);
  --bg-offset: hsl(0, 0%, 100%);
  --text: hsl(200, 15%, 8%);
  --gray: hsl(0, 0%, 52%);
  --border: rgba(0, 0, 0, 0.1);
}
ul,
ol {
  list-style: none;
}
body {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}
h1,
h2 {
  // height: 18px;
}
.wrapper {
  // width: 95%;
  // max-width: 1140px;
  // margin: 0 auto;
  // margin-left: 3rem;
  // margin: 4.2em 2em 0em 7em;
  margin: 5.5em 1.5em 0em 6.5em;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 1.5em;
  margin-top: -1em;
}
.card {
  background-color: var(--bg-offset);
  padding: 0px;
  box-shadow: 0px 2px 4px var(--border);
  transition: all 0.3s cubic-bezier(0.075, 0.82, 0.165, 1);
  text-align: center;

  &:hover {
    transform: scale(1.05);

    .card-content {
      h3 {
        display: block;
        -webkit-line-clamp: none;
        -webkit-box-orient: none;
        overflow: visible;
      }
    }
  }
}
.card-image {
  img {
    // height: 130px;
    width: 100%;
    object-fit: cover;
    object-position: center;
    height: 150px;
  }
}
.card-content {
  padding-bottom: 10px;

  h3 {
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-style: bold;
    margin-top: 1em;
  }
}
.card-list {
  margin-top: 10px;

  li {
    color: var(--text);
    // margin-top: 8px;

    span {
      color: var(--gray);
    }
  }
}

/* search input */
.search-wrapper {
  margin: 0em 0em;
  display: flex;
  justify-content: flex-start;
}

select {
  width: 99%;
  background-color: var(--bg-offset);
  background-size: 16px 16px;
  background-position: left 10px center;
  background-repeat: no-repeat;
  padding: 1em 0.5em;
  padding-left: 0.5em;
  border: 1px solid var(--border);
  box-shadow: 0px 4px 6px var(--border);
  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  border-radius: 5px;

  &:hover {
    box-shadow: 0px 0px 0px var(--border);
  }
}

.select {
  margin-right: 0.9em;
}

.search-box {
  width: "250px";
  margin: "0px 40px 0px 20px";
  background-color: "#efefef";
  box-shadow: "none";
  height: 30px;
}

@media (max-width: 1024px) {
  .search-input {
    width: 100%;
  }
  .search-wrapper {
    justify-content: start;
    flex-wrap: wrap;
  }
  .wrapper {
    margin: 4em 1em 1em 1em;
  }
  .select {
    margin-left: 0px;
  }
}

.search-input {
  width: 94%;
  background-image: url("data:image/svg+xml;base64,");
  background-color: var(--bg-offset);
  background-size: 16px 16px;
  background-position: left 30px center;
  background-repeat: no-repeat;
  padding: 0.83em 1em;
  padding-left: 4em;
  border: 1px solid var(--border);
  color: hsl(200, 15%, 8%);
  box-shadow: 0px 4px 6px var(--border);
  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  font-size: 15px;
  border-radius: 5px;

  &:hover {
    box-shadow: 0px 0px 0px var(--border);
  }
}

.movie-app > .row {
  overflow-x: auto;
  flex-wrap: nowrap;
}

.image-container {
  position: relative;
  transition: transform 0.2s;
}

.image-container:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}
